.scroll-to-top {
    background: #17a2b8;
    width: 40px;
    height: 40px;
    position: fixed;
    right: 20px;
    bottom: 60px;
    z-index: 999;
    margin: 0;
    padding: 0;
    border: none;
    border-radius: 6px;
    background-image: url('./up-arrow.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 24px;
    cursor: pointer;
    opacity: 0.6;
    transition: 0.2s;
}

.scroll-to-top:hover {
    opacity: 1;
}
